import React, { Component } from 'react';
import { Link } from "react-router-dom";
import ImageLoader from 'react-loading-image'
import Loader from 'react-loader-spinner'

import logo from '../TLSLogo.png';
import img from '../tls-camera.jpg'
import cover from '../private.jpg'
import '../App.css';

const API = 'https://enigmatic-lake-22259.herokuapp.com';
// const API = 'http://localhost:3000';

class Galleries extends Component {

  constructor() {
    super();
    this.state = {
      opacity: 0,
      albums: [],
      grouped_albums: []
    }
  }

  componentDidMount() {
    this.getGroupedAlbums().then(
      this.getAlbums()
    )
  }

  async getAlbums() {
    fetch(`${API}/albums`, {
      method: 'GET',
      mode: 'cors',
      headers: {
        'Access-Control-Allow-Origin': '*',
        Accept: 'application/json',
        'Content-Type': 'application/json'
      }
    })
    .then(response => response.json())
    .then((responseJson) => {
      let sortedResp = []
      if (this.props.match.params.id) {
        sortedResp = responseJson.filter(obj => {
          let result = {album: obj.album, images: obj.images.reverse()}
          if (result.album.album_group_id == this.props.match.params.id) {
            return result
          }
        })
      } else {
        let grouped_albums_arr = []
        sortedResp = responseJson.map(obj => {
          let album_group_id = obj.album.album_group_id
          if (album_group_id && !grouped_albums_arr.includes(album_group_id)) {

            grouped_albums_arr.push(obj.album.album_group_id)
            return this.state.grouped_albums.find(function(element) {
              if(element.id == album_group_id) {
                return element
              }

            });
          } else if (!album_group_id) {
            let result = {album: obj.album, images: obj.images.reverse()}
            return result
          }
        }).filter(obj => { return obj !== undefined })
      }
      this.setState({
        albums: sortedResp.reverse()
      })

      console.log('=====API albums GET=====', sortedResp)
    })
    .catch(error => {
      console.log('=====API error=====', error)
    })
  }


    async getGroupedAlbums() {
      fetch(`${API}/album_groups`, {
        method: 'GET',
        mode: 'cors',
        headers: {
          'Access-Control-Allow-Origin': '*',
          Accept: 'application/json',
          'Content-Type': 'application/json'
        }
      })
      .then(response => response.json())
      .then((responseJson) => {
        // let sortedResp = []
        // if (this.props.match.params.id) {
        //   sortedResp = responseJson.filter(obj => {
        //     let result = {album: obj.album, images: obj.images.reverse()}
        //     if (result.album.album_group_id == this.props.match.params.id) {
        //       return result
        //     }
        //   })
        // } else {
        //   // TO DO: PREVENT GROUPED ALBUMS FROM ALL DISPLAYING
        //   // SEARCH FOR GROUPED_ALBUM_ID
        //   sortedResp = responseJson.map(obj => {
        //     let result = {album: obj.album, images: obj.images.reverse()}
        //     return result
        //   })
        // }
        this.setState({
          grouped_albums: responseJson
        })

        console.log('=====API GROUPED_ALBUMS GET=====', responseJson)
      })
      .catch(error => {
        console.log('=====API error=====', error)
      })
    }



  fadeIn(ms=0) {
    let _this = this;
    setTimeout(function(){
      _this.setState({
        opacity: 1,
      })
    }, ms);
  }

  fadeOut(ms=0) {
    let _this = this;
    setTimeout(function(){
      _this.setState({
        opacity: 0,
      })
    }, ms);
  }



renderAlbums() {
    let array = [];
    for (var i = 0; i < this.state.albums.length; i++) {
      // console.log(this.state.albums);
      if (this.state.albums[i].album) {
        array.push(
          <Link to={{ pathname: `/gallery/${this.state.albums[i].album.id}`, state: { album: this.state.albums[i]} }}>
            <div className="album-card">
              <ImageLoader
                className="img-itm"
                src={this.state.albums[i].images[this.state.albums[i].album.cover].url}
                loading={() =>
                  <Loader
                    type="RevolvingDot"
                    color="#616161"
                    height="30"
                    width="30"
                    />
                }
                error={() => <div>Loading...</div>}
                />
              <img unselectable="on" src={cover} className="img-cover" alt="cover" />
              <div className="album-n"></div>
              <p className="album-title">{this.state.albums[i].album.title}</p>
            </div>
          </Link>
        )
      } else {
        array.push(
          <Link to={{ pathname: `/grouped_galleries/${this.state.albums[i].id}` }}>
            <div className="album-card">
              <ImageLoader
                className="img-itm"
                src={this.state.albums[i].cover}
                loading={() =>
                  <Loader
                    type="RevolvingDot"
                    color="#616161"
                    height="30"
                    width="30"
                    />
                }
                error={() => <div>Loading...</div>}
                />
              <img unselectable="on" src={cover} className="img-cover" alt="cover" />
              <div className="album-n"></div>
              <p className="album-title">{this.state.albums[i].title}</p>
            </div>
          </Link>
        )
      }
    }
    return array;
  }

  render() {
    return (
      <div style={{height: 'auto', position: 'relative'}}>

        <img
          unselectable="on" src={logo}
          className="logo-2" alt="logo"
        />

        <div className="img-gal">
          {
            this.renderAlbums()
          }
        </div>
      </div>
    );
  }
}

export default Galleries;
